.Container{
  padding: 2%;
  padding-bottom: 5%;
}

.Box{
  text-align: center;
  padding: 3%;
  margin: 0px 30% 0px 30%;
  border-radius: 5px;
  border-width: 2px;
}

.Content{
  display: inline-block;
}

.FormGroup{
  display: block;
}

.Heads{
  color: black;
  font-size: 30px;
  margin: 0px 0px 60px 0px;
}

.Subheads{
  display: block;
  font-size: 18px;
}

.InputBoxes{
  width: 300px;
  padding: 10px;
  margin: 10px;
  margin-bottom: 0px;
  border-radius: 50px;
  border-style: solid;
  border-width: 2px;
  border-color: black;
}

input[type=submit]{
  color: white;
  background-color: black;
  margin: 20px;
  margin-bottom: 40px;
}

.Select:hover, .Smbtn:hover, .Topbar:hover, .Navbut:hover{
  cursor: pointer;
  color: #999999;
}

.Smbtn{
  display: inline-block;
  width: 45%;
  margin: 0%;
  margin-top: 0px;
}

.Passbox{
  width: 300px !important;
  padding: 12px !important;
  margin: 20px;
  margin-bottom: 0px;
  border-radius: 50px !important;
  border-style: solid !important;
  border-width: 2px;
  font-size: 15px !important;
}

.ReactPasswordStrength-strength-bar{
  margin: 1px 25px 10px 45px;
  max-width: 293px;
  padding: 3px;
}

.ReactPasswordStrength.is-strength-0 .ReactPasswordStrength-strength-bar{
  width: calc(0% + 2px);
}

.ReactPasswordStrength.is-strength-1 .ReactPasswordStrength-strength-bar{
  width: calc(20% + 2px);
}

.ReactPasswordStrength.is-strength-2 .ReactPasswordStrength-strength-bar{
  width: calc(40% + 2px);
}

.ReactPasswordStrength.is-strength-3 .ReactPasswordStrength-strength-bar{
  width: calc(60% + 2px);
}

.ReactPasswordStrength.is-strength-4 .ReactPasswordStrength-strength-bar{
  width: calc(80% + 2px);
}

.ReactPasswordStrength-strength-desc{
  font-size: 0px;
}

.Container2{
  padding: 1%;
  padding-bottom: 1.5%;
}

.Topbar{
  height: 5%;
  margin-left: 5%;
}

.Userlogo{
  width: 30px;
  margin-left: 20px;
}

.Userloginlogo{
  width: 300px;
  margin: 20px;
}

.Navbut{
  border-width: 0px;
  border-bottom-width: 2px;
  padding: 10px;
  width: 40px;
  color: white;
}